.now-on-view {
  @include page-grid;

  &__wrapper {
    display: grid;
    grid-column: span 2;
    row-gap: 48px;

    @include on-tablet {
      grid-column: span 6;
    }

    @include on-desktop {
      grid-column: span 12;
    }
  }

  &__title {
    grid-column: span 2;
    display: flex;
    flex-direction: column;
    row-gap: 8px;
    margin-bottom: 48px;

    @include on-tablet {
      grid-column: span 6;
    }

    @include on-desktop {
      grid-column: span 12;
    }
  }

  &__text {
    margin: 0;

    font-weight: 400;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
  }
}
